import React, { Component, forwardRef } from 'react';
import FullCalendar from '@fullcalendar/react';
import timelinePlugin from '@fullcalendar/timeline';
import resourceTimelinePlugin from '@fullcalendar/resource-timeline';
import moment from 'moment-timezone';
import 'moment/locale/fr';
import { fr, enAU, es, it, de } from 'date-fns/locale';
import DatePicker, { registerLocale } from 'react-datepicker';
import frLocale from '@fullcalendar/core/locales/fr';
import enLocale from '@fullcalendar/core/locales/en-au';
import esLocale from '@fullcalendar/core/locales/es';
import itLocale from '@fullcalendar/core/locales/it';
import deLocale from '@fullcalendar/core/locales/de';
import 'antd/dist/antd.css';
import { Container, Row, Col } from 'react-bootstrap';
import 'react-datepicker/dist/react-datepicker.css';
import 'react-bootstrap-typeahead/css/Typeahead.css';
import { Typeahead } from 'react-bootstrap-typeahead';
import { connect } from 'react-redux';
import rightArrow from '../../styles/right-arrow.png';
import minus from '../../styles/minus.png';
import {
  getRessourceSalle,
  getRessourceLieu,
  getRessourceUf,
  getResourceMotif,
  getResourcesPraticien,
  getEventsOfRessource,
  getRessourcePlanningOfSalle,
} from '../../utils/fetcher';
import { Tooltip } from 'bootstrap';
import calendar from '../../styles/calendar.png';

moment.locale('fr');
if (localStorage.getItem('rtc-lang') === 'fr') registerLocale('fr', fr);
else if (localStorage.getItem('rtc-lang') === 'en') registerLocale('enAU', enAU);
else if (localStorage.getItem('rtc-lang') === 'es') registerLocale('es', es);
else if (localStorage.getItem('rtc-lang') === 'it') registerLocale('it', it);
else if (localStorage.getItem('rtc-lang') === 'de') registerLocale('de', de);

const grayColor = '#EBEBEB';
const whiteColor = '#ffffff';
let chooseColor = '';

class Timeline extends Component {
  calendarComponentRef = React.createRef();
  typeHeaderSalleRef = React.createRef();
  typeHeaderLieuRef = React.createRef();
  typeHeaderUfRef = React.createRef();
  typeHeaderMotifRef = React.createRef();

  constructor(props) {
    console.log('V - 9');
    super(props);
    this.state = {
      locale: frLocale,
      selectedDayAgenda: null,
      selectedDay: new Date(),
      dateWeek: null,
      datasResource: null,
      datasSalle: [],
      selectedSalle: '',
      datasLieu: [],
      selectedLieu: '',
      datasUf: [],
      selectedUf: '',
      datasMotif: [],
      selectedMotif: '',
    };
    this.handleChange = this.handleChange.bind(this);
  }
  loadSalle = () => {
    var tab = [];
    this.props.getRessourceSalleFunction();
    setTimeout(() => {
      if (this.props.ressourcesSalle) {
        this.props.ressourcesSalle.forEach((ress) => {
          tab.push(ress);
        });
      }
      this.setState({
        datasSalle: tab,
      });
    }, 1000);
  };
  loadByMotif = () => {
    var tab = [];
    this.props.getResourceMotifFunction();
    setTimeout(() => {
      if (this.props.ressourcesMotif) {
        this.props.ressourcesMotif.forEach((ress) => {
          tab.push(ress);
        });
      }
      this.setState({
        datasMotif: tab,
      });
    }, 1000);
  };
  loadByLieu = () => {
    var tab = [];
    this.props.getRessourceLieuFunction();
    setTimeout(() => {
      if (this.props.ressourcesLieu) {
        this.props.ressourcesLieu.forEach((ress) => {
          tab.push(ress);
        });
      }
      this.setState({
        datasLieu: tab,
      });
    }, 1000);
  };
  loadByUf = () => {
    var tab = [];
    this.props.getRessourceUfFunction();
    setTimeout(() => {
      if (this.props.ressourcesUf) {
        this.props.ressourcesUf.forEach((ress) => {
          tab.push(ress);
        });
      }
      this.setState({
        datasUf: tab,
      });
    }, 1000);
  };
  componentDidMount = () => {
    if (localStorage.getItem('rtc-lang') === 'fr') this.setState({ locale: frLocale });
    else if (localStorage.getItem('rtc-lang') === 'en') this.setState({ locale: enLocale });
    else if (localStorage.getItem('rtc-lang') === 'es') this.setState({ locale: esLocale });
    else if (localStorage.getItem('rtc-lang') === 'it') this.setState({ locale: itLocale });
    else if (localStorage.getItem('rtc-lang') === 'de') this.setState({ locale: deLocale });
    // await this.setState({ isAwaiting: true });
    this.props.getResourcesPraticienFunction();
    setTimeout(() => {
      var dateDebutPeriode = new Date(this.state.dateWeek);
      var momentObjDebut = moment(dateDebutPeriode);
      var momentStringDebut = momentObjDebut.format('YYYY-MM-DD');
      var startOf = moment(momentStringDebut).startOf('isoweek').toJSON();
      var endOf = moment(momentStringDebut).endOf('isoweek').add(1, 'weeks').toJSON();
      this.props.getEventsOfRessourceFunction(
        this.state.ress ? this.state.ress.map((data) => data.id) : '',
        startOf,
        endOf
      );
    }, 1000);
    this.loadSalle();
    this.loadByMotif();
    this.loadByLieu();
    this.loadByUf();
    this.positionCalendar();
  };
  /**
   * Pour reposition l'icone calendrier a cote du boution Aujourd'hui
   */
  positionCalendar = () => {
    // if (!this.state.isAwaiting) {
    //Recuperation de l'element dans lequel est place l'icone de calendrier
    let parentIconCalendar = document.getElementById('icon-calendar');
    //Recuperation de l'icone
    let iconCalendar = document.getElementsByClassName('react-datepicker-wrapper')[0];
    //Suppresion de l'icone de son conteneur original
    parentIconCalendar.removeChild(iconCalendar);
    //Recuperation de l'element ou est ranger le bouton aujourd'hui
    let div = document
      .getElementsByClassName('fc-header-toolbar fc-toolbar fc-toolbar-ltr')[0]
      .getElementsByClassName('fc-toolbar-chunk')[0];
    //Recuperation du bouton
    let button = div.getElementsByClassName('fc-today-button fc-button fc-button-primary')[0];
    //Insersion de l'icone dans le container du bouton, et le place avant le bouton
    div.insertBefore(iconCalendar, button);
    // }
  };
  handleChange = (selectedDayAgenda) => {
    this.setState({ selectedDayAgenda }, () => {
      this.setState({ selectedDay: this.state.selectedDayAgenda }, () => {
        this.calendarComponentRef.current.getApi().gotoDate(this.state.selectedDay);
      });
    });
  };
  /**
   * Fonction appele lors de la modification de la plage du calendrier
   * @param arg (Info du calendar)
   */
  datesRender = async (arg) => {
    this.customResourceTimelineWeek(arg.view.type);
    this.setState({ dateWeek: arg.view.activeStart });
    if (this.calendarComponentRef.current !== null) {
      await this.calendarComponentRef.current.getApi().removeAllEvents();
      this.calendarComponentRef.current.getApi().addEventSource(this.props.eventsOfRessources);
    }
    if (arg.view.type === 'resourceTimelineDay' || arg.view.type === 'resourceTimelineWeek') {
      const dateDebutPeriode = new Date(arg.view.activeStart);
      const momentObjDebut = moment(dateDebutPeriode);
      const momentStringDebut = momentObjDebut.format('YYYY-MM-DD');
      const startOf = moment(momentStringDebut).startOf('isoweek').toJSON();
      const endOf = moment(momentStringDebut).endOf('isoweek').add(1, 'weeks').toJSON();
      this.props.getEventsOfRessourceFunction(
        this.state.ress ? this.state.ress.map((data) => data.id) : '',
        startOf,
        endOf
      );
    } else if (arg.view.type === 'resourceTimelineMonth') {
      const dateDebutPeriode = new Date(arg.view.activeStart);
      const momentObjDebut = moment(dateDebutPeriode);
      const momentStringDebut = momentObjDebut.format('YYYY-MM-DD');
      const startOf = moment(momentStringDebut).startOf('isoweek').toJSON();
      const endOf = moment(momentStringDebut).endOf('isoweek').add(1, 'months').toJSON();
      this.props.getEventsOfRessourceFunction(
        this.state.ress ? this.state.ress.map((data) => data.id) : '',
        startOf,
        endOf
      );
    }
  };
  /**
   * Fonction de personnalisation du calendrier
   * @param viewType (Le type de view active sur le calendrier - Jour - Semaine - Mois)
   */
  customResourceTimelineWeek = (viewType) => {
    let thElementTitleResource = document.getElementsByClassName('fc-datagrid-header')[0].getElementsByTagName('th')[0];
    thElementTitleResource.removeAttribute('style');

    let thElementJour = Object.values(
      document.getElementsByClassName('fc-timeline-header-row')[0].getElementsByTagName('th')
    );
    thElementJour.forEach((el) => {
      el.removeAttribute('style');
    });

    let slot = Object.values(document.getElementsByClassName('fc-timeline-slot-lane'));

    var tr = document.getElementById('matinSoirTr');
    var trHour = document.getElementById('trHour');
    if (tr !== null) {
      tr.parentNode.removeChild(tr);
    }
    if (trHour !== null) {
      trHour.setAttribute('style', 'display: contents');
    }

    if (viewType === 'resourceTimelineWeek') {
      var nbrColumn = 7;
      var journee = true;
      let hourTab = document
        .getElementsByClassName('fc-resourceTimelineWeek-view')[0]
        .getElementsByTagName('thead')[0]
        .getElementsByTagName('td')[2]
        .getElementsByTagName('tbody')[0];
      var trHourNew = hourTab.getElementsByTagName('tr')[1];
      trHourNew.setAttribute('id', 'trHour');
      trHourNew.setAttribute('style', 'display: none');
      var trNew = document.createElement('tr');
      trNew.setAttribute('id', 'matinSoirTr');
      trNew.setAttribute('class', 'fc-timeline-header-row');
      hourTab.appendChild(trNew);
      for (let i = 1; i <= 84; i = i + 6) {
        var td = document.createElement('td');
        if (nbrColumn === 5) {
          nbrColumn = 7;
        } else {
          nbrColumn = 5;
        }
        td.setAttribute('colspan', nbrColumn);
        td.setAttribute('style', 'text-align: center; border: 1px solid rgba(0, 0, 0, 0.56) !important');
        let text = undefined;
        if (journee) {
          text = document.createTextNode('Matin');
          journee = false;
        } else {
          text = document.createTextNode('Aprs midi');
          journee = true;
        }
        td.appendChild(text);
        document.getElementById('matinSoirTr').appendChild(td);
      }

      thElementTitleResource.setAttribute('style', 'padding: 10px');
      thElementJour.forEach((el) => {
        el.setAttribute('style', 'padding: 10px');
      });

      var isMatin = true;
      slot.forEach((el, index) => {
        if (index % 6 === 0 && index !== 0) {
          if (isMatin) {
            slot[index - 1].setAttribute('style', 'border-left: 2px solid #464646');
            slot[index].setAttribute('style', 'border: none');
            isMatin = false;
          } else {
            el.setAttribute('style', 'border: 2px solid #464646');
            isMatin = true;
          }
        } else {
          el.setAttribute('style', 'border: none');
        }
      });
    } else {
      slot.forEach((el) => {
        el.setAttribute('style', 'border: 2px solid #464646');
      });
    }
  };
  /**
   * Fonction de choix le medecin ou la salle
   */
  chooseSalle = async (ress) => {
    await this.setState({
      selectedSalle: ress,
    });
    this.filterDatas();
  };
  /**
   * Fonction de choix du Motif
   */
  chooseMotif = async (ress) => {
    await this.setState({
      selectedMotif: ress,
    });
    this.filterDatas();
  };
  /**
   * Fonction de choix du lieu
   */
  chooseLieu = async (ress) => {
    if (ress.length > 0) {
      this.filterUFByLieu(ress[0].id_lieu);
    } else {
      this.filterUFByLieu(null);
    }
    await this.setState({
      selectedLieu: ress,
    });
    this.filterDatas();
  };
  /**
   * Fonction de choix de l'UF
   */
  chooseUf = async (ress) => {
    await this.setState({
      selectedUf: ress,
    });
    this.filterDatas();
  };
  /**
   * Filtre UF en fonction du lieu
   */
  filterUFByLieu = async (lieu_id = null) => {
    var tab = [];
    if (lieu_id === null) {
      this.loadByUf();
    } else {
      this.props.getRessourceUfFunction();
      setTimeout(() => {
        if (this.props.ressourcesUf) {
          this.props.ressourcesUf.forEach((ress) => {
            if (ress.id_lieu === lieu_id) tab.push(ress);
          });
        }
        this.setState({
          datasUf: tab,
        });
      }, 1000);
    }
  };
  /**
   * Filtre Generale
   */
  filterDatas = async () => {
    if (this.state.selectedSalle.length > 0) {
      let tab = [];
      await this.state.selectedSalle.forEach((element) => {
        tab.push(
          this.props.ressourcesSalle.filter((entry) =>
            Object.values(entry).some((val) => typeof val === 'string' && val.includes(element.id))
          )[0]
        );
      });
      await this.setState({ datasResource: tab });
    } else {
      await this.setState({ datasResource: null });
    }

    if (this.state.selectedMotif.length > 0) {
      let tab = [];
      let eventFiltre = [];
      await this.state.selectedMotif.forEach((element) => {
        this.props.eventsOfRessources.forEach((entry) => {
          entry.listemotif.forEach((motif) => {
            if (element.id_motif_rdv === motif.id_motif_rdv) eventFiltre.push(entry);
          });
        });
      });
      if (this.state.datasResource === null) {
        await eventFiltre.forEach((event) => {
          tab.push(this.props.ressourcesSalle.filter((entry) => entry.id === event.resourceId)[0]);
        });
        await this.setState({ datasResource: tab });
      } else {
        let datasResourceEvent = [];
        await eventFiltre.forEach((event) => {
          datasResourceEvent.push(this.props.ressourcesSalle.filter((entry) => entry.id === event.resourceId)[0]);
        });
        datasResourceEvent = Array.from(new Set(datasResourceEvent.map((entry) => entry.id))).map((id) => {
          return datasResourceEvent.find((entry) => entry.id === id);
        });
        this.setState({
          datasResource: this.state.datasResource.filter((element) => datasResourceEvent.includes(element)),
        });
      }
    }

    if (this.state.selectedLieu.length > 0) {
      let tab = [];
      let eventFiltre = await this.props.eventsOfRessources.filter(
        (entry) => entry.idlieu === this.state.selectedLieu[0].id_lieu
      );
      if (this.state.datasResource === null) {
        console.log(eventFiltre);
        await eventFiltre.forEach((event) => {
          tab.push(this.props.ressourcesSalle.filter((entry) => entry.id === event.resourceId)[0]);
        });
        await this.setState({ datasResource: tab });
      } else {
        let datasResourceEvent = [];
        await eventFiltre.forEach((event) => {
          datasResourceEvent.push(this.props.ressourcesSalle.filter((entry) => entry.id === event.resourceId)[0]);
        });
        datasResourceEvent = Array.from(new Set(datasResourceEvent.map((entry) => entry.id))).map((id) => {
          return datasResourceEvent.find((entry) => entry.id === id);
        });
        this.setState({
          datasResource: this.state.datasResource.filter((element) => datasResourceEvent.includes(element)),
        });
      }
    }

    if (this.state.selectedUf.length > 0) {
      let tab = [];
      let eventFiltre = await this.props.eventsOfRessources.filter(
        (entry) => entry.uf === this.state.selectedUf[0].id_uf
      );
      if (this.state.datasResource === null) {
        await eventFiltre.forEach((event) => {
          tab.push(this.props.ressourcesSalle.filter((entry) => entry.id === event.resourceId)[0]);
        });
        await this.setState({ datasResource: tab });
      } else {
        let datasResourceEvent = [];
        await eventFiltre.forEach((event) => {
          datasResourceEvent.push(this.props.ressourcesSalle.filter((entry) => entry.id === event.resourceId)[0]);
        });
        datasResourceEvent = Array.from(new Set(datasResourceEvent.map((entry) => entry.id))).map((id) => {
          return datasResourceEvent.find((entry) => entry.id === id);
        });
        this.setState({
          datasResource: this.state.datasResource.filter((element) => datasResourceEvent.includes(element)),
        });
      }
    }
  };

  eventDidMount = (info) => {
    if (info.event.extendedProps.type === 'conge') {
      let startDate = info.event.start;
      let endDate = info.event.end;

      if (startDate >= endDate) {
        endDate = new Date(startDate);
        endDate.setMinutes(endDate.getMinutes() + 5);
      }

      let startDay =
        startDate.getDate() >= 0 && startDate.getDate() < 10 ? '0' + startDate.getDate() : '' + startDate.getDate();
      let startMonth =
        startDate.getMonth() >= 0 && startDate.getMonth() < 10
          ? '0' + (startDate.getMonth() + 1)
          : '' + (startDate.getMonth() + 1);
      let startYear = startDate.getFullYear();

      let endDay = endDate.getDate() >= 0 && endDate.getDate() < 10 ? '0' + endDate.getDate() : '' + endDate.getDate();
      let endMonth =
        endDate.getMonth() >= 0 && endDate.getMonth() < 10
          ? '0' + (endDate.getMonth() + 1)
          : '' + (endDate.getMonth() + 1);
      let endYear = endDate.getFullYear();

      var content = info.event.title + ' <br />';
      content = content + 'Date de dbut: ' + startDay + '/' + startMonth + '/' + startYear + '<br />';
      content = content + 'Date de fin: ' + endDay + '/' + endMonth + '/' + endYear + '<br />';

      new Tooltip(info.el, {
        title: content,
        placement: 'top',
        trigger: 'hover',
        container: 'body',
        html: true,
      });
    } else {
      let motif = '';
      info.event.extendedProps.listemotif.forEach((element) => {
        motif = motif + ' ' + element.abreviation_motif_rdv;
      });

      if (info.view.type === 'resourceTimelineDay') {
        var contentTitle = 'Motif: ' + motif + ' <br />';
        contentTitle = contentTitle + 'UF: ' + info.event.extendedProps.nom_uf + '<br />';
        contentTitle = contentTitle + 'Salle: ' + info.event.extendedProps.nom_salle + '<br />';
        new Tooltip(info.el, {
          title: contentTitle,
          placement: 'top',
          trigger: 'hover',
          container: 'body',
          html: true,
        });
      } else {
        let startDate = info.event.start;
        let endDate = info.event.end;

        if (startDate >= endDate) {
          endDate = new Date(startDate);
          endDate.setMinutes(endDate.getMinutes() + 5);
        }

        let date =
          startDate.getDate() >= 0 && startDate.getDate() < 10 ? '0' + startDate.getDate() : '' + startDate.getDate();
        let month =
          startDate.getMonth() >= 0 && startDate.getMonth() < 10
            ? '0' + (startDate.getMonth() + 1)
            : '' + (startDate.getMonth() + 1);
        let year = startDate.getFullYear();

        let hourStart =
          startDate.getHours() >= 0 && startDate.getHours() < 10
            ? '0' + startDate.getHours()
            : '' + startDate.getHours();
        let hourEnd =
          endDate.getHours() >= 0 && endDate.getHours() < 10 ? '0' + endDate.getHours() : '' + endDate.getHours();
        let minuteStart =
          startDate.getMinutes() >= 0 && startDate.getMinutes() < 10
            ? '0' + startDate.getMinutes()
            : '' + startDate.getMinutes();
        let minuteEnd =
          endDate.getMinutes() >= 0 && endDate.getMinutes() < 10
            ? '0' + endDate.getMinutes()
            : '' + endDate.getMinutes();

        var contentTooltip = 'Motif: ' + motif + ' <br />';
        contentTooltip = contentTooltip + 'Date: ' + date + '/' + month + '/' + year + '<br />';
        contentTooltip = contentTooltip + 'Horaire: ' + hourStart + 'h' + minuteStart + ' -> ' + hourEnd + 'h' + minuteEnd + '<br />';
        contentTooltip = contentTooltip + 'UF: ' + info.event.extendedProps.nom_uf + '<br />';
        contentTooltip = contentTooltip + 'Salle: ' + info.event.extendedProps.nom_salle + '<br />';

        new Tooltip(info.el, {
          title: contentTooltip,
          placement: 'top',
          trigger: 'hover',
          container: 'body',
          html: true,
        });
      }
    }
  };
  eventContent = (info) => {
    if (info.event.extendedProps.type === 'conge') {
      return <div className="conge">CONG</div>;
    } else {
      if (info.view.type === 'resourceTimelineDay') {
        let startDate = info.event.start;
        let endDate = info.event.end;

        if (startDate >= endDate) {
          endDate = new Date(startDate);
          endDate.setMinutes(endDate.getMinutes() + 5);
        }

        let hourStart =
          startDate.getHours() >= 0 && startDate.getHours() < 10
            ? '0' + startDate.getHours()
            : '' + startDate.getHours();
        let hourEnd =
          endDate.getHours() >= 0 && endDate.getHours() < 10 ? '0' + endDate.getHours() : '' + endDate.getHours();
        let minuteStart =
          startDate.getMinutes() >= 0 && startDate.getMinutes() < 10
            ? '0' + startDate.getMinutes()
            : '' + startDate.getMinutes();
        let minuteEnd =
          endDate.getMinutes() >= 0 && endDate.getMinutes() < 10
            ? '0' + endDate.getMinutes()
            : '' + endDate.getMinutes();
        return (
          <div className="content-event-align-vert">
            {hourStart + 'h' + minuteStart}
            <img alt="Right arrow" src={rightArrow} style={{ height: '10px', marginLeft: '10px', marginRight: '10px' }} />
            {hourEnd + 'h' + minuteEnd}
            <img alt="Minus" src={minus} style={{ height: '10px', marginLeft: '10px', marginRight: '10px' }} />
            {info.event.extendedProps.nbrrdv} RDV(s)
          </div>
        );
      } else {
        return <div>{info.event.extendedProps.nbrrdv} RDV(s)</div>;
      }
    }
  };
  resourceLabelDidMount = (info) => {
    if (chooseColor === '' || chooseColor === grayColor) {
      chooseColor = whiteColor;
    } else if (chooseColor === whiteColor) {
      chooseColor = grayColor;
    }
    info.el.style.backgroundColor = chooseColor;
    let table = document.getElementsByClassName('fc-scrollgrid-sync-table')[3];
    let element = table.querySelectorAll("td[data-resource-id='" + info.resource.id + "']")[0];
    element.style.backgroundColor = chooseColor;
    // return 'eeee';
  };
  resourceLabelContent = (info) => {
    return (
      <div>
        <p className="resource_p">{info.fieldValue}</p>
        <p className="resource_p">{info.resource.extendedProps.tel}</p>
      </div>
    );
  };
  render() {
    const IconCalendar = forwardRef(({ value, onClick }, ref) => (
      <img alt="calendar icon" src={calendar} className={'icon-calendar'} ref={ref} onClick={onClick} />
    ));
    return (
      <Container fluid>
        <Row>
          <Col className={'padding-img-calendar'} id={'icon-calendar'}>
            <DatePicker
              {...this.props}
              showWeekNumbers
              selected={this.state.selectedDayAgenda}
              onChange={this.handleChange}
              dateFormat="Pp"
              locale={fr}
              customInput={<IconCalendar />}
            />
          </Col>
        </Row>
        <Row className="form-search">
          <Col sm={3} className="zone">
            <label className="label-form-search">Salle :</label>
            <Typeahead
              multiple
              ref={this.typeHeaderSalleRef}
              id="typeheaderSalle"
              options={this.state.datasSalle}
              placeholder="Rechercher..."
              emptyLabel="Rechercher ..."
              labelKey={(option) => `${option.title}`}
              size="small"
              onChange={(ress) => this.chooseSalle(ress)}
              onFocus={this.onFocusTypeHeaderSalle}
            />
          </Col>
          <Col sm={3} className="zone">
            <label className="label-form-search">Motif :</label>
            <Typeahead
              multiple
              ref={this.typeHeaderMotifRef}
              id="typeheadMotif"
              options={this.state.datasMotif}
              placeholder="Rechercher..."
              emptyLabel="Rechercher ..."
              className="typeheadPraticiensBox"
              labelKey={(option) => `${option.libelle_motif_rdv}`}
              size="small"
              onChange={(ress) => this.chooseMotif(ress)}
              onFocus={this.onFocusTypeHeaderMotif}
            />
          </Col>
          <Col sm={3} className="zone">
            <label className="label-form-search">Lieux :</label>
            <Typeahead
              ref={this.typeHeaderLieuRef}
              id="typeheadLieu"
              options={this.state.datasLieu}
              placeholder="Rechercher..."
              emptyLabel="Rechercher ..."
              className="typeheadPraticiensBox"
              labelKey={(option) => `${option.libelle_lieu}`}
              size="small"
              selected={this.state.selectedLieu}
              onChange={(ress) => this.chooseLieu(ress)}
              onFocus={this.onFocusTypeHeaderLieu}
            />
          </Col>
          <Col sm={3} className="zone">
            <label className="label-form-search">UF :</label>
            <Typeahead
              ref={this.typeHeaderUfRef}
              id="typeheadUf"
              options={this.state.datasUf}
              placeholder="Rechercher..."
              emptyLabel="Rechercher ..."
              className="typeheadPraticiensBox"
              labelKey={(option) => `${option.libelle_lieu}`}
              size="small"
              onChange={(ress) => this.chooseUf(ress)}
              onFocus={this.onFocusTypeHeaderUf}
            />
          </Col>
        </Row>
        <Row id="planningTimeline">
          <Col>
            <FullCalendar
              schedulerLicenseKey="CC-Attribution-NonCommercial-NoDerivatives"
              ref={this.calendarComponentRef}
              locale={this.state.locale}
              datesSet={this.datesRender}
              displayEventEnd={true}
              slotMinTime={'08:00'}
              slotMaxTime={'20:00'}
              slotDuration={'01:00:00'}
              plugins={[resourceTimelinePlugin, timelinePlugin]}
              initialView="resourceTimelineDay"
              resourceOrder="title"
              headerToolbar={{
                left: 'today',
                center: 'prev,title,next',
                right: 'resourceTimelineDay,resourceTimelineWeek',
              }}
              resourceAreaWidth="15%"
              resourceAreaHeaderContent="Salle"
              resources={
                this.props.ressourcesSalle
                  ? this.state.datasResource === null
                    ? this.props.ressourcesSalle
                    : this.state.datasResource
                  : []
              }
              resourceLabelContent={this.resourceLabelContent}
              events={this.props.eventsOfRessources}
              eventDidMount={this.eventDidMount}
              eventContent={this.eventContent}
              resourceLabelDidMount={this.resourceLabelDidMount}
            />
          </Col>
        </Row>
      </Container>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    ressourcesSalle: state.ressourcesTimeline.ressourcesSalle,
    ressourcesMotif: state.ressourcesTimeline.ressourcesMotif,
    ressourcesLieu: state.ressourcesTimeline.ressourcesLieu,
    ressourcesUf: state.ressourcesTimeline.ressourcesUf,

    ressourcesTimeline: state.ressourcesTimeline.ressources,
    eventsOfRessources: state.ressourcesTimeline.eventsOfRessources,

    ressourcesPlanningOfSalle: state.ressourcesSalle.ressourcesPlanningOfSalle,
    ressourcesOfSalle: state.ressourcesSalle.ressourcesOfSalle,
  };
};
const mapDispatchToProps = (dispatch) => {
  return {
    getRessourceSalleFunction: () => dispatch(getRessourceSalle()),
    getResourceMotifFunction: () => dispatch(getResourceMotif()),
    getRessourceLieuFunction: () => dispatch(getRessourceLieu()),
    getRessourceUfFunction: () => dispatch(getRessourceUf()),

    getResourcesPraticienFunction: () => dispatch(getResourcesPraticien()),
    getEventsOfRessourceFunction: (identifiant_user, startOf, endOf) =>
      dispatch(getEventsOfRessource(identifiant_user, startOf, endOf)),

    getRessourcePlanningOfSalleFunction: (id_salle, startOf, endOf) =>
      dispatch(getRessourcePlanningOfSalle(id_salle, startOf, endOf)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Timeline);
