import ky from "ky";
import cookies from "js-cookies";

import { setRessourcesTimeline, getRessourcesSalle } from "../actions";

const isDev = process.env.NODE_ENV === "development";
const fetcherAuth = () => {
    let _fetcher;
    _fetcher = ky.extend({
        prefixUrl: isDev?process.env.REACT_APP_URL_ORIGIN:window.location.origin,
        retry: 1,
        credentials: "same-origin",
        headers: {
            "x-alx-idc": isDev ? process.env.REACT_APP_IDC : cookies.getItem("x-alx-idc"),
            "Authorization":`${isDev ? process.env.REACT_APP_LOGIN : cookies.getItem("x-alx-login")} ${isDev ? process.env.REACT_APP_TOKEN:cookies.getItem("x-alx-token")}`,
        }
    });
    return _fetcher;
};

export const getPractitioners = () => {
    return fetcherAuth()
        .get('api/api_praticien/ressource_praticien/')
            .then(response => response.json())
            .then(json => {
                if(json.result === 'ok'){
                    return {practitioners: json.tabinfo, success: true}
                }else{
                    return {success:false, error: json.message ? json.message : 'Erreur !'}
                }
            })
            .catch(err => {
                return {success: false, error: err}
            })
}

export const getRooms = () => {
    return fetcherAuth()
        .get('api/api_praticien/ressource_praticien?type=box')
            .then(response => response.json())
            .then(json => {
                if(json.result === 'ok'){
                    return {rooms: json.tabinfo, success: true}
                }else{
                    return {success:false, error: json.message ? json.message : 'Erreur !'}
                }
            })
            .catch(err => {
                return {success: false, error: err}
            })
}

export const getResourcesPraticien = () => {
    return (dispatch, getState) => {
        fetcherAuth()
            .get('api/api_praticien/ressource_praticien/')
                .then(response => response.json())
                .then(json => {
                    if(json.result === 'ok'){
                        const ressources  = {ressources :json.tabinfo};
                        dispatch(setRessourcesTimeline(ressources));
                    }
                });
    }
}

export const getFeedEvents = (startDate, endDate) => {
    return fetcherAuth()
        .get(`api/api_praticien/ressource_timeline?start=${startDate}&end=${endDate}`)
        .then(response => response.json())
        .then(json => {
            return {feed:json.tabinfo, success: true};
        })
        .catch(err => {
            return {feed: [], success: false, error: err};
        })
}

export const getLocations = () => {
    const idc = cookies.getItem("x-alx-idc");
    return fetcherAuth()
            .get(`api/api_lieu/lieu?guid_entite=${idc}`)
            .then(response => response.json())
            .then(json => {
                return {locations: json.tabinfo, success: true}
            })
            .catch(err => {
                return {locations: [], success: false, error: err}
            })
    
}

export const getEventsOfRessource = (identifiant_user, startOf, endOf) => {
    return (dispatch, getState) => {
        fetcherAuth()
            .get(`api/api_praticien/ressource_timeline?start=${startOf}&end=${endOf}&identifiant_user=${identifiant_user?identifiant_user:''}`)
            .then(response => response.json())
            .then(json => {
                const ressources  = {eventsOfRessources :json.tabinfo}
                dispatch(setRessourcesTimeline(ressources));
            })
            .catch(err => {
                console.log(err)
            })
    }
}

