import React from 'react';
import { HashRouter, Switch, Route } from 'react-router-dom';
import PlanningTimeline from './components/Planning/PlanningTimeline';
import Timeline from './components/Timeline/Timeline';

export default function RouterAlx() {
  return (
    <HashRouter>
      <Switch>
        {/*<Route exact path="/login" component={Login}/>*/}
        {/*<Route exact path="/" component={Planning}/>*/}
        {/*<Route exact path="/teleconsultation" component={Teleconsultation}/>*/}
        {/*<Route exact path="/structure" component={Structure}/>*/}
        {/*<Route exact path="/activites" component={Activite}/>*/}
        {/*<Route exact path="/motifs" component={Motifs}/>*/}
        {/*<Route exact path="/creermotif" component={CreerMotif}/>*/}
        {/*<Route exact path="/creermotif/:guidmotif" component={CreerMotif}/>*/}
        {/*<Route exact path="/agendaRDV" component={AgendaRDV}/>*/}
        {/*<Route exact path="/reponseEmail" component={Reponse}/>*/}
        <Route exact path="/planningTimeline" component={PlanningTimeline} />
        <Route exact path="/timeline-salle" component={Timeline} />
        {/*<Route exact path="/messagerie" component={Messagerie}/>*/}
        {/*<Route exact path="/droituser" component={Droit}/>*/}
      </Switch>
    </HashRouter>
  );
}
