import { Checkbox, Switch } from 'antd';
import React, { useState } from 'react';
import { Typeahead } from 'react-bootstrap-typeahead';

const TypeaheadInput = ({
  label,
  options,
  labelKey,
  onChange,
  onFocus,
  refs,
  id,
  isPracticien,
  switchToggle,
  switchDefaultChecked }) => {
  const [selectedOptions, setSelectedOptions] = useState([]);
  const [selectAll, setSelectAll] = useState(false);

  const handleChange = (selected) => {
    onChange(selected);
    if (selected.length === options.length) {
      setSelectAll(true);
    } else {
      setSelectAll(false);
    }
    setSelectedOptions(selected);
  };

  const handleChecked = (e) => {
    setSelectAll(e.target.checked);
    if (e.target.checked) {
      setSelectedOptions(options);
      onChange(options)
    } else {
      setSelectedOptions([]);
      onChange([])
    }
  }

  const renderToken = (option, props, idx) => {
    const maxVisibleOptions = 1;
    if (idx >= maxVisibleOptions + 1 && selectAll){
      return null
    }
    const shouldTruncate = selectedOptions.length === options.length  && idx >= maxVisibleOptions;
    return shouldTruncate ? (
      <span className='rbt-token rbt-token-removeable'>
        +{ selectedOptions.length - maxVisibleOptions } autres
      </span>
    ) : (
        <div className='rbt-token rbt-token-removeable' key={ idx }>
          { option?.title || option?.libelle_motif_rdv || option?.libelle_lieu }
        <button className="close rbt-close rbt-token-remove-button"
            onClick={ () => props.onRemove(option) }
        >
          <span aria-hidden="true"></span>
          <span className="sr-only visually-hidden">Remove</span>
        </button>
      </div>
    );
  };
  return (
    <>
      { label && <div style={ { display: 'flex', justifyContent: 'space-between' } }> <label className="label-form-search">{ label } </label><Checkbox className="label-form-search" checked={ selectAll } onChange={ handleChecked } > Tous</Checkbox> </div> }
      { isPracticien &&
        <div style={ { display: 'flex', justifyContent: 'space-between' } }>
          <div>
            <label className="label-form-search">Rechercher selon</label>
            <Switch
              className="switch-form-search"
              size="large"
              checkedChildren="Praticien"
              unCheckedChildren="Salle"
              defaultChecked={ switchDefaultChecked }
              onChange={ switchToggle }
            />
          </div>
          <Checkbox className="label-form-search" checked={ selectAll } onChange={ handleChecked } > Tous</Checkbox>
        </div>
      }
      <Typeahead
        clearButton={ true }
        selected={ selectedOptions }
        multiple
        ref={ refs }
        id={ id }
        options={ options }
        className="typeheadPraticiensBox"
        labelKey={ labelKey }
        onChange={ handleChange }
        onFocus={ onFocus }
        placeholder="Rechercher..."
        emptyLabel="Rechercher ..."
        size="small"
        renderToken={ renderToken }
        shouldSelect
        paginate
      />
    </>
  );
};

export default TypeaheadInput;