import React, { Component, forwardRef } from "react";
import FullCalendar from "@fullcalendar/react";
import timelinePlugin from "@fullcalendar/timeline";
import resourceTimelinePlugin from "@fullcalendar/resource-timeline";
import moment from "moment-timezone";
import "moment/locale/fr";
import { fr, enAU, es, it, de } from "date-fns/locale";
import DatePicker, { registerLocale } from "react-datepicker";
import frLocale from "@fullcalendar/core/locales/fr";
import enLocale from "@fullcalendar/core/locales/en-au";
import esLocale from "@fullcalendar/core/locales/es";
import itLocale from "@fullcalendar/core/locales/it";
import deLocale from "@fullcalendar/core/locales/de";
import "antd/dist/antd.css";
import {
  Container,
  Row,
  Col,
  ProgressBar,
  OverlayTrigger,
  Tooltip,
} from "react-bootstrap";
import "react-datepicker/dist/react-datepicker.css";
import TypeaheadItems from "./FilterPlanning/TypeaheadItems";
import { connect } from "react-redux";
import rightArrow from "../../styles/right-arrow.png";
import minus from "../../styles/minus.png";
import LoadingSpinner from "../../utils/LoadingSpinner";
import {
  getResourcesPraticien,
  getEventsOfRessource,
  getRessourceByType,
  getRessourceLieu,
  getRessourceUf,
  getResourceMotif,
  getFeedEvents,
  getLocations,
  getPractitioners,
  getRooms,
} from "../../utils/fetcher";
import calendar from "../../styles/calendar.png";
import { setRessourcesTimeline } from "../../actions";
moment.locale("fr");
if (localStorage.getItem("rtc-lang") === "fr") registerLocale("fr", fr);
else if (localStorage.getItem("rtc-lang") === "en")
  registerLocale("enAU", enAU);
else if (localStorage.getItem("rtc-lang") === "es") registerLocale("es", es);
else if (localStorage.getItem("rtc-lang") === "it") registerLocale("it", it);
else if (localStorage.getItem("rtc-lang") === "de") registerLocale("de", de);
const grayColor = "#EBEBEB";
const whiteColor = "#ffffff";
let chooseColor = "";
let loading = false;
class PlanningTimeline extends Component {
  calendarComponentRef = React.createRef();
  typeHeaderPraticienBoxRef = React.createRef();
  typeHeaderLieuRef = React.createRef();
  typeHeaderUfRef = React.createRef();
  typeHeaderMotifRef = React.createRef();
  startTime = 8;
  endTime = 20;
  eventsLoaded = [];
  constructor(props) {
    console.log("V - 1.02");
    super(props);
    this.state = {
      locale: frLocale,
      selectedDayAgenda: null,
      type: true,
      selectedDay: new Date(),
      dateWeek: null,
      selectedPraticienSalle: [],
      datasLieu: [],
      datasResourceFixed: [],
      roomsResourceFixed: [],
      ressourcesToShow: [],
      selectedLieu: [],
      datasUf: [],
      selectedUf: [],
      datasMotif: [],
      selectedMotif: [],
      calendarEvents: {},
      headerPercent: undefined,
      actualView: undefined,
      datasResource: [],
      currentDate: "",
      currentWeek: {},
    };
  }

  loadByMotif = () => {
    this.props.getResourceMotifFunction();
  };
  loadByLieu = async () => {
    const result = await getLocations();
    if (result.success) {
      this.setState({ datasLieu: result.locations });
    } else {
      console.error(
        result.error ? result.error : "Erreur sur la rcupration des lieux !"
      );
    }
  };
  loadByUf = () => {
    var tab = [];
    this.props.getRessourceUfFunction();
    setTimeout(() => {
      if (this.props.ressourcesUf) {
        this.props.ressourcesUf.forEach((ress) => {
          tab.push(ress);
        });
      }
      this.setState({
        datasUf: tab,
      });
    }, 1000);
  };
  componentDidMount = async () => {
    this._isMounted = true;
    let newState = {};
    let locale = undefined;
    switch (localStorage.getItem("rtc-lang")) {
      case "fr":
        locale = frLocale;
        break;
      case "en":
        locale = enLocale;
        break;
      case "es":
        locale = esLocale;
        break;
      case "it":
        locale = itLocale;
        break;
      case "de":
        locale = deLocale;
        break;
      default:
        locale = frLocale;
    }
    newState["locale"] = locale;

    const practitioners = await getPractitioners();
    if (practitioners.success) {
      this.props.setRessources({ ressources: practitioners.practitioners });
      newState["datasResourceFixed"] = practitioners.practitioners;
      this.filterDatas(practitioners.practitioners);
    } else {
      console.error(practitioners.error);
    }
    const rooms = await getRooms();
    if (rooms.success) {
      newState["roomsResourceFixed"] = rooms.rooms;
    }
    this.setState(newState);
    this.loadByMotif();
    this.loadByLieu();
    this.loadByUf();
    this.positionCalendar();
  };

  componentWillUnmount() {
    this._isMounted = false;
  }
  /**
   * Pour reposition l'icone calendrier a cote du boution Aujourd'hui
   */
  positionCalendar = () => {
    //Recuperation de l'element dans lequel est place l'icone de calendrier
    let parentIconCalendar = document.getElementById("icon-calendar");
    //Recuperation de l'icone
    let iconCalendar = document.getElementsByClassName(
      "react-datepicker-wrapper"
    )[0];
    //Suppresion de l'icone de son conteneur original
    parentIconCalendar.removeChild(iconCalendar);
    //Recuperation de l'element ou est ranger le bouton aujourd'hui

    const toolbar = document.getElementsByClassName(
      "fc-header-toolbar fc-toolbar fc-toolbar-ltr"
    )[0];
    let div = toolbar.getElementsByClassName("fc-toolbar-chunk")[0];
    //Recuperation du bouton
    let button = div.getElementsByClassName(
      "fc-today-button fc-button fc-button-primary"
    )[0];
    //Insersion de l'icone dans le container du bouton, et le place avant le bouton
    div.insertBefore(iconCalendar, button);

    const percentHeader = document.getElementById("percentHeader");
    toolbar.after(percentHeader);
  };
  handleChange = (selectedDayAgenda) => {
    this.setState({ selectedDayAgenda: selectedDayAgenda });
    this?.calendarComponentRef?.current?.getApi()?.gotoDate(selectedDayAgenda);
  };
  handleSwitchChange = (type) => {
    this.setState({ type: type });
  };
  /**
   * Fonction de choix le medecin ou la salle
   */
  choosePraticienSalle = async (ress) => {
    await this.setState({
      selectedPraticienSalle: ress,
    });
    this.filterDatas();
  };
  /**
   * Fonction de choix du Motif
   */
  chooseMotif = async (ress) => {
    await this.setState({
      selectedMotif: ress,
    });
    this.filterDatas();
  };
  /**
   * Fonction de choix du lieu
   */
  chooseLieu = async (ress) => {
    if (ress.length > 0) {
      this.filterUFByLieu(ress[0].id_lieu);
    } else {
      this.filterUFByLieu(null);
    }
    await this.setState({
      selectedLieu: ress,
    });
    this.filterDatas();
  };
  /**
   * Fonction de choix de l'UF
   */
  chooseUf = async (ress) => {
    await this.setState({
      selectedUf: ress,
    });
    this.filterDatas();
  };
  /**
   * Filtre UF en fonction du lieu
   */
  filterUFByLieu = async (lieu_id = null) => {
    var tab = [];
    if (lieu_id === null) {
      this.loadByUf();
    } else {
      this.props.getRessourceUfFunction();
      setTimeout(() => {
        if (this.props.ressourcesUf) {
          this.props.ressourcesUf.forEach((ress) => {
            if (ress.id_lieu === lieu_id) {
              tab.push(ress);
            }
          });
        }
        this.setState({
          datasUf: tab,
        });
      }, 1000);
    }
  };
  /**
   * Filtre Generale
   */
  filterDatas = (ressources = []) => {
    if (ressources.length === 0) {
      return [];
    }

    let ressourcesToShow = ressources;

    if (this.state.selectedPraticienSalle.length > 0) {
      ressourcesToShow = this.state.selectedPraticienSalle;
    }

    if (this.state.selectedMotif.length > 0) {
      const eventFiltre = this.eventsLoaded
        .filter((entry) =>
          this.state.selectedMotif.some((element) =>
            entry.listemotif.some(
              (elem) => elem.id_motif_rdv === element.id_motif_rdv
            )
          )
        )
        .map((entry) => entry.resourceId);
      ressourcesToShow = ressourcesToShow.filter((elem) =>
        eventFiltre.includes(elem.id)
      );
    }

    if (this.state.selectedLieu.length > 0) {
      const eventFiltre = this.eventsLoaded
        .filter((entry) => entry.idlieu === this.state.selectedLieu[0].id_lieu)
        .map((entry) => entry.resourceId);
      ressourcesToShow = ressourcesToShow.filter((elem) =>
        eventFiltre.includes(elem.id)
      );
    }

    if (this.state.selectedUf.length > 0) {
      const eventFiltre = this.eventsLoaded
        .filter((entry) => entry.uf === this.state.selectedUf[0].id_uf)
        .map((entry) => entry.resourceId);
      ressourcesToShow = ressourcesToShow.filter((elem) =>
        eventFiltre.includes(elem.id)
      );
    }

    return ressourcesToShow;
  };

  eventContent = (info) => {
    const key =
      info.event.extendedProps.type +
      info.event.start?.getTime() +
      "-" +
      info.event.end?.getTime();

    if (info.event.extendedProps.type === "conge") {
      var content = (
        <div>
          {info.event.title}
          <br />
          {"Date de dbut: " + info.event.start.toLocaleDateString()}
          <br />
          {"Date de fin: " + info.event.end.toLocaleDateString()}
          <br />
        </div>
      );

      return (
        <OverlayTrigger
          key={key}
          placement={"bottom"}
          overlay={<Tooltip id={`tooltip-${key}`}>{content}</Tooltip>}
        >
          <div className="conge">CONG</div>
        </OverlayTrigger>
      );
    } else {
      let motif = "";
      info.event.extendedProps.listemotif.forEach((element, index) => {
        if (index > 0) {
          motif += ", ";
        }
        motif +=
          element.abreviation_motif_rdv !== ""
            ? element.abreviation_motif_rdv
            : element.libelle_motif_rdv.slice(0, 5);
      });

      const rdvmin = info.event.extendedProps.rdvmin;
      const rdvmax = info.event.extendedProps.rdvmax;
      const appointmentsTaken = info.event.extendedProps.appointmentsTaken;
      const isInterval = rdvmin !== rdvmax;
      if (info.view.type === "resourceTimelineDay") {
        var contentTitle = (
          <div>
            {"Motif: " + motif}
            <br />
            {info.event.extendedProps.nom_uf && (
              <span>
                {" "}
                UF: {info.event.extendedProps.nom_uf} <br />{" "}
              </span>
            )}
            {info.event.extendedProps.nom_salle && (
              <span>
                {" "}
                Salle: {info.event.extendedProps.nom_salle} <br />
              </span>
            )}
            {info.event.extendedProps.nbrrdv}
          </div>
        );

        let startDate = info.event.start;
        let endDate = info.event.end;

        if (startDate >= endDate) {
          endDate = new Date(startDate);
          endDate.setMinutes(endDate.getMinutes() + 5);
        }

        const startTime = moment(startDate).format("HH[h]mm");
        const endTime = moment(endDate).format("HH[h]mm");
        return (
          <OverlayTrigger
            key={key}
            placement="bottom"
            overlay={<Tooltip id={`tooltip-${key}`}>{contentTitle}</Tooltip>}
          >
            <div className="content-event-align-vert">
              {startTime}
              <img
                alt="Right arrow"
                src={rightArrow}
                style={{
                  height: "10px",
                  marginLeft: "10px",
                  marginRight: "10px",
                }}
              />
              {endTime}
              <img
                alt="Minus"
                src={minus}
                style={{
                  height: "10px",
                  marginLeft: "10px",
                  marginRight: "10px",
                }}
              />
              <strong>Dispo :&nbsp;</strong>
              {isInterval ? rdvmin + " - " + rdvmax : rdvmax} |{" "}
              <strong>&nbsp; Pris :&nbsp;</strong>
              {appointmentsTaken} RDV(s)
            </div>
          </OverlayTrigger>
        );
      } else {
        const startDate = info.event.start;
        let endDate = info.event.end;

        if (startDate >= endDate) {
          endDate = new Date(startDate);
          endDate.setMinutes(endDate.getMinutes() + 5);
        }

        const startMoment = moment(startDate);
        const endMoment = moment(endDate);
        const contentTooltip = (
          <div>
            {"Motif: " + motif}
            <br />
            {"Date: " + startMoment.format("DD/MM/YYYY")}
            <br />
            {"Horaire: " +
              startMoment.format("HH[h]mm") +
              " -> " +
              endMoment.format("HH[h]mm")}
            <br />
            {info.event.extendedProps.nom_uf && (
              <span>
                {" "}
                UF: {info.event.extendedProps.nom_uf} <br />{" "}
              </span>
            )}
            {info.event.extendedProps.nom_salle && (
              <span>
                {" "}
                Salle: {info.event.extendedProps.nom_salle} <br />
              </span>
            )}
            {info.event.extendedProps.nbrrdv}
          </div>
        );
        return (
          <OverlayTrigger
            key={key}
            placement="bottom"
            overlay={<Tooltip id={`tooltip-${key}`}>{contentTooltip}</Tooltip>}
          >
            <div>
              <strong>Dispo : </strong>{" "}
              {isInterval ? rdvmin + " - " + rdvmax : rdvmax} |{" "}
              <strong> Pris : </strong>
              {appointmentsTaken} RDV(s)
            </div>
          </OverlayTrigger>
        );
      }
    }
  };

  resourceLabelDidMount = (info) => {
    if (chooseColor === "" || chooseColor === grayColor) {
      chooseColor = whiteColor;
    } else if (chooseColor === whiteColor) {
      chooseColor = grayColor;
    }
    info.el.style.backgroundColor = chooseColor;
    let table = document.getElementsByClassName("fc-scrollgrid-sync-table")[3];
    let element = table.querySelectorAll(
      "td[data-resource-id='" + info.resource.id + "']"
    )[0];
    element.style.backgroundColor = chooseColor;
  };
  resourceLabelContent = (info) => {
    return (
      <div>
        <p className="resource_p">{info.fieldValue}</p>
        <p className="resource_p">{info.resource.extendedProps.tel}</p>
      </div>
    );
  };
  getSlotLabel = (info) => {
    if (info.view.type === "resourceTimelineWeek" && info.level === 1) {
      if (info.date.getHours() === this.startTime) {
        return <span className="headerDivision">Matin</span>;
      } else {
        return <span className="headerDivision">Aprs-midi</span>;
      }
    } else if (info.view.type === "resourceTimelineMonth" && info.level === 0) {
      info.text = "Semaine " + info.text;
    } else if (info.view.type === "resourceTimelineMonth" && info.level === 1) {
      return <span className="headerDivision">{info.text}</span>;
    }
    return info.text;
  };
  // Fonction permettant de verifier si une date fait partie d'une range de dates
  isDateInRange = (date, startDate, endDate) => {
  const dateObj = new Date(date);
  const startObj = new Date(startDate);
  const endObj = new Date(endDate);
  return dateObj >= startObj && dateObj <= endObj;
  }

  getHeaderToolbar = (view, eventsCalendar) => {
    if (view !== "resourceTimelineWeek" && view !== "resourceTimelineDay") {
      return;
    }

    const calendarEvents = eventsCalendar;
    const events = [];
    const headerRessourceWidth = document?.getElementsByClassName("fc-datagrid-header")[0]?.style?.width;
    if (view === "resourceTimelineWeek") {
      for (const elem in calendarEvents) {
        // Verification de l'appartenance de la date la semaine courante
        let isInRange = this.isDateInRange(moment(elem, "DD/MM/YYYY").toString(), this.state.currentWeek.start, this.state.currentWeek.end )
        let toAdd = { taken: 0, min: 0, max: 0 };
        if (isInRange || Object.keys(calendarEvents).length < 30) {
          calendarEvents[elem].forEach((planning) => {
            if (this.state.datasResource.includes(planning.resourceId)) {
              if (typeof planning.min !== "number") {
                planning.min = 0;
              }
              if (typeof planning.max !== "number") {
                planning.max = 0;
              }
              toAdd.min += planning.min;
              toAdd.max += planning.max;
              toAdd.taken += planning.taken;
            }
          });
          events.push(toAdd);
        }
      }
    } else if (view === "resourceTimelineDay") {
      const midDay = this.startTime + (this.endTime - this.startTime) / 2;
      const currentDateDay = moment(this.state.selectedDayAgenda).format(
        "DD/MM/YYYY"
        );
        for (const elem in calendarEvents) {
        let toAddMorning = {
          taken: 0,
          mat: 0,
          min: 0,
          max: 0,
          minmat: 0,
          maxmat: 0,
        };
        let toAddAfternoon = {
          taken: 0,
          apm: 0,
          min: 0,
          max: 0,
          minapm: 0,
          maxapm: 0,
        };
        if (currentDateDay === elem) {
          calendarEvents[elem].forEach((planning) => {
            const midDate = new Date(planning.timeStart);
            midDate.setHours(midDay);
            midDate.setMinutes(0);
            midDate.setSeconds(0);
            if (
              this.state.datasResource.includes(planning.resourceId) &&
              planning.timeEnd > planning.timeStart
            ) {
              if (typeof planning.min !== "number") {
                planning.min = 0;
              }
              if (typeof planning.max !== "number") {
                planning.max = 0;
              }
              if (typeof planning.mat !== "number") {
                planning.mat = 0;
              }
              if (typeof planning.apm !== "number") {
                planning.apm = 0;
              }
              if (typeof planning.minmat !== "number") {
                planning.minmat = 0;
              }
              if (typeof planning.maxmat !== "number") {
                planning.maxmat = 0;
              }
              if (typeof planning.minapm !== "number") {
                planning.minapm = 0;
              }
              if (typeof planning.maxapm !== "number") {
                planning.maxapm = 0;
              }
              toAddMorning.minmat += planning.minmat;
              toAddMorning.maxmat += planning.maxmat;
              toAddMorning.mat += planning.mat;
              toAddAfternoon.minapm += planning.minapm;
              toAddAfternoon.maxapm += planning.maxapm;
              toAddAfternoon.apm += planning.apm;
            }
          });
          events.push(toAddMorning, toAddAfternoon);
        }
      }
    }

    return (
      <div style={{ marginLeft: headerRessourceWidth, display: "flex" }}>
        {events.map((value, index) => {
          const nbrOfRdv = value.mat
            ? value.mat
            : value.apm
            ? value.apm
            : value.taken;
          const allMin = value.minmat
            ? value.minmat
            : value.minapm
            ? value.minapm
            : value.min;
          const allMax = value.maxmat
            ? value.maxmat
            : value.maxapm
            ? value.maxapm
            : value.max;
          const percent =
            allMax > 0 ? (nbrOfRdv * 100) / (allMax + nbrOfRdv) : 0;

          return (
            <div
              style={{ flex: "1", margin: "0 5px" }}
              key={"dayPercent" + index}
            >
              <div style={{ display: "flex", justifyContent: "space-between" }}>
                <span style={{ color: "#5D9AD4" }}>{nbrOfRdv}</span>
                <span>
                  ({allMin} - {allMax})
                </span>
              </div>
              <ProgressBar style={{ height: "10px" }} now={percent} />
            </div>
          );
        })}
      </div>
    );
  };

  eventSourceGetPlannings = (info, successCallback, failureCallback) => {
    const startDate = moment(info.start).toJSON();
    const endDate = moment(info.end).toJSON();
    getFeedEvents(startDate, endDate).then((result) => {
      if (result && result.success) {
        this.eventsLoaded = result.feed;
        this.updateHeaderInfo(result.feed, info.start, info.end);
        successCallback(result.feed);
      }
    });
  };

  compareEvents = (events1, events2) => {
    if (Object.keys(events1).length !== Object.keys(events2).length) {
      return false;
    }

    for (const elem in events1) {
      if (!(elem in events2) || events1[elem].length !== events2[elem].length) {
        return false;
      }
    }
    return true;
  };

  updateHeaderInfo = (events, startSearch, endSearch) => {
    let headerEvents = {};
    const endDateString = endSearch.toLocaleDateString();
    let indexDate = startSearch;
    while (indexDate.toLocaleDateString() !== endDateString) {
      const indexDateStr = indexDate.toLocaleDateString();
      headerEvents[indexDateStr] = [];
      indexDate.setDate(indexDate.getDate() + 1);
    }

    events.forEach((elem) => {
      if (elem.type === "planning") {
        const start = new Date(elem.start);
        const end = new Date(elem.end);
        const startDate = start.toLocaleDateString();
        const objectToPush = {
          taken: elem.appointmentsTaken,
          mat: elem.rdvmat,
          apm: elem.rdvapm,
          max: elem.rdvmax,
          min: elem.rdvmin,
          maxmat: elem.rdvmaxmat,
          minmat: elem.rdvminmat,
          maxapm: elem.rdvmaxapm,
          minapm: elem.rdvminapm,
          resourceId: elem.resourceId,
          timeStart: start,
          timeEnd: end,
        };
        if (startDate in headerEvents) {
          headerEvents[startDate].push(objectToPush);
        } else {
          console.error("Date retrieved not in interval searched !");
        }
      }
    });
    if (!this.compareEvents(headerEvents, this.state.calendarEvents)) {
      this.setState({ calendarEvents: headerEvents });
    }
  };
  loadingFunction = (isLoading) => {
    if (isLoading) {
      loading = true;
      if (document.getElementsByClassName("spinner-container-show")[0]) {
        document.getElementsByClassName(
          "spinner-container-show"
        )[0].style.display = "block";
      }
    } else {
      loading = false;
      if (document.getElementsByClassName("spinner-container-show")[0]) {
        document.getElementsByClassName(
          "spinner-container-show"
        )[0].style.display = "none";
      }
    }
  };

  render() {
    const IconCalendar = forwardRef(({ value, onClick }, ref) => (
      <img
        alt="Calendar Icon"
        src={calendar}
        className={"icon-calendar"}
        ref={ref}
        onClick={onClick}
      />
    ));
    const actualView = this.state.actualView;
    const showHeaderPercent =
      actualView &&
      (actualView === "resourceTimelineWeek" ||
        actualView === "resourceTimelineDay");
    return (
      <Container fluid>
        {loading && (
          <Row style={{ paddingBottom: "10px" }}>
            <LoadingSpinner />
          </Row>
        )}
        <Row>
          <Col className={"padding-img-calendar"} id={"icon-calendar"}>
            <DatePicker
              {...this.props}
              showWeekNumbers
              selected={this.state.selectedDayAgenda}
              onChange={this.handleChange}
              dateFormat="Pp"
              locale={fr}
              customInput={<IconCalendar />}
            />
          </Col>
        </Row>
        <Row
          id="percentHeader"
          style={{
            display: showHeaderPercent ? "block" : "none",
            padding: "0 15px",
            marginBottom: "5px",
          }}
        >
          {this.getHeaderToolbar(actualView, this.state.calendarEvents)}
        </Row>
        <Row className="form-search">
          <TypeaheadItems
            ressourcesMotif={this.props.ressourcesMotif}
            datasResourceFixed={this.state.datasResourceFixed}
            roomsResourceFixed={this.state.roomsResourceFixed}
            type={this.state.type}
            chooseMotif={this.chooseMotif}
            handleSwitchChange={this.handleSwitchChange}
            choosePraticienSalle={this.choosePraticienSalle}
            onFocusTypeHeaderMotif={this.onFocusTypeHeaderMotif}
            typeHeaderMotifRef={this.typeHeaderMotifRef}
            datasLieu={this.state.datasLieu}
            selectedLieu={this.state.selectedLieu}
            chooseLieu={this.chooseLieu}
            onFocusTypeHeaderLieu={this.onFocusTypeHeaderLieu}
            onFocusTypeHeaderPraticienBox={this.onFocusTypeHeaderPraticienBox}
            typeHeaderLieuRef={this.typeHeaderLieuRef}
            datasUf={this.state.datasUf}
            chooseUf={this.chooseUf}
            onFocusTypeHeaderUf={this.onFocusTypeHeaderUf}
            typeHeaderUfRef={this.typeHeaderUfRef}
          />
        </Row>
        <Row id="planningTimeline">
          <Col>
            <FullCalendar
              schedulerLicenseKey="CC-Attribution-NonCommercial-NoDerivatives"
              ref={this.calendarComponentRef}
              locale={this.state.locale}
              displayEventEnd={true}
              slotMinTime={this.startTime + ":00"}
              slotMaxTime={this.endTime + ":00"}
              plugins={[resourceTimelinePlugin, timelinePlugin]}
              initialView="resourceTimelineDay"
              resourceOrder="title"
              headerToolbar={{
                left: "today",
                center: "prev,title,next",
                right:
                  "resourceTimelineDay,resourceTimelineWeek,resourceTimelineMonth",
              }}
              resourceAreaWidth="15%"
              resourceAreaHeaderContent={
                this.state.type ? "Praticiens" : "Salle"
              }
              resources={async (
                fetchInfo,
                successCallback,
                failureCallback
              ) => {
                const ressourcesTyped = this.state.type
                  ? this.state.datasResourceFixed
                  : this.state.roomsResourceFixed;
                if (ressourcesTyped.length > 0) {
                  const ressources = await this.filterDatas(ressourcesTyped);
                  const ressourcesIds = [];
                  ressources.forEach((elem) => {
                    if (!ressourcesIds.includes(elem.id)) {
                      ressourcesIds.push(elem.id);
                    }
                  });
                  if (
                    this.state.datasResource.length !== ressourcesIds.length
                  ) {
                    this.setState({ datasResource: ressourcesIds });
                  }
                  successCallback(ressources);
                } else {
                  successCallback([]);
                }
              }}
              resourceLabelContent={this.resourceLabelContent}
              loading={this.loadingFunction}
              eventContent={this.eventContent}
              events={this.eventSourceGetPlannings}
              slotLabelContent={this.getSlotLabel}
              stickyHeaderDates={true}
              expandRows={true}
              contentHeight="auto"
              datesSet={(datesInfo) => {
                if (datesInfo.view.type !== this.state.actualView) {
                  this.setState({
                    actualView: datesInfo.view.type,
                    selectedDayAgenda: datesInfo.start,
                    currentDate: datesInfo.view.activeStart,
                    currentWeek: { start: datesInfo.view.activeStart, end: datesInfo.view.activeEnd },
                  });
                } else {
                  this.setState({ selectedDayAgenda: datesInfo.start });
                }
              }}
              views={{
                month: {
                  slotLabelFormat: [
                    { week: "numeric" }, // top level of text
                    { weekday: "narrow", day: "2-digit" },
                  ],
                },
                week: {
                  slotDuration: (this.endTime - this.startTime) / 2 + ":00:00",
                  slotLabelFormat: [
                    { weekday: "short", day: "2-digit", month: "2-digit" },
                    { hour: "2-digit" },
                  ],
                },
                day: {
                  slotDuration: "01:00:00",
                },
              }}
            />
          </Col>
        </Row>
      </Container>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    ressourcesTimeline: state.ressourcesTimeline.ressources,
    eventsOfRessources: state.ressourcesTimeline.eventsOfRessources,
    ressourcesByType: state.ressourcesTimeline.ressourcesByType,
    ressourcesMotif: state.ressourcesTimeline.ressourcesMotif,
    ressourcesLieu: state.ressourcesTimeline.ressourcesLieu,
    ressourcesUf: state.ressourcesTimeline.ressourcesUf,
  };
};
const mapDispatchToProps = (dispatch) => {
  return {
    getResourcesPraticienFunction: () => dispatch(getResourcesPraticien()),
    getEventsOfRessourceFunction: (identifiant_user, startOf, endOf) =>
      dispatch(getEventsOfRessource(identifiant_user, startOf, endOf)),
    getRessourceByTypeFunction: (type) => dispatch(getRessourceByType(type)),
    getResourceMotifFunction: () => dispatch(getResourceMotif()),
    getRessourceLieuFunction: () => dispatch(getRessourceLieu()),
    getRessourceUfFunction: () => dispatch(getRessourceUf()),
    setRessources: (ressources) => dispatch(setRessourcesTimeline(ressources)),
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(PlanningTimeline);
