import React, { Component } from "react";
import Col from "react-bootstrap/Col";
import TypeaheadInput from "./TypeaheadInput";

class TypeaheadItems extends Component {
  render() {
    return (
      <>
        <Col sm={ 3 } className="zone">
          <TypeaheadInput
            options={
              this.props.type
                ? this.props.datasResourceFixed
                : this.props.roomsResourceFixed
            }
            labelKey={ (option) => `${option.title}` }
            onChange={ (ress) => this.props.choosePraticienSalle(ress) }
            onFocus={ this.onFocusTypeHeaderPraticienBox }
            refs={ this.typeHeaderPraticienBoxRef }
            id="typeheadPraticienBox"
            selectAll={ this.props.selectAllPraticienSalle }
            isPracticien={ true }
            switchToggle={ (type) => this.props.handleSwitchChange(type) }
            switchDefaultChecked={ this.props.type }
          />
        </Col>
        <Col sm={ 3 } className="zone">
          <TypeaheadInput
            label="Motif :"
            options={
              this.props.ressourcesMotif ? this.props.ressourcesMotif : []
            }
            labelKey={ (option) => `${option.libelle_motif_rdv}` }
            onChange={ (ress) => this.props.chooseMotif(ress) }
            onFocus={ this.props.onFocusTypeHeaderMotif }
            refs={ this.props.typeHeaderMotifRef }
            id="typeheadMotif"
          />

        </Col>
        <Col sm={ 3 } className="zone">
          <TypeaheadInput
            label="Lieux :"
            options={ this.props.datasLieu }
            labelKey={ (option) => `${option.libelle_lieu}` }
            selected={ this.props.selectedLieu ? this.props.selectedLieu : [] }
            onChange={ (ress) => this.props.chooseLieu(ress) }
            onFocus={ this.props.onFocusTypeHeaderLieu }
            refs={ this.props.typeHeaderLieuRef }
            id="typeheadLieu"
          />
        </Col>
        <Col sm={ 3 } className="zone">
          <TypeaheadInput
            label="UF :"
            options={ this.props.datasUf }
            labelKey={ (option) => `${option.libelle_lieu}` }
            onChange={ (ress) => this.props.chooseUf(ress) }
            onFocus={ this.props.onFocusTypeHeaderUf }
            refs={ this.props.typeHeaderUfRef }
            id="typeheadUf"
          />
        </Col>
      </>
    );
  }
}

export default TypeaheadItems;
